module.exports = "\r\n/* <------- Navigation Bar Styling ------->*/\r\n\r\n.scroll_main{\r\n  position: relative;\r\n  font-size: 30px;\r\n  padding-right: 10px;\r\n  top:15px;\r\n  color:white !important;\r\n  transition:.5s;\r\n}\r\n\r\n.head_text{\r\n  width:400px;\r\n  height: 60px;\r\n  margin:0 80px;\r\n  transition:.5s;\r\n  border-radius: 8px;\r\n}\r\n\r\n.head_text h1 {\r\n  font-size: 40px;\r\n  font-family: 'Nunito', sans-serif;\r\n  text-align: center;\r\n  border:2px solid white;\r\n  border-radius: 8px;\r\n  transition:.5s;\r\n}\r\n\r\n.head_text:hover h1{\r\n  color: rgb(107, 87, 124);\r\n  border:2px solid rgb(107, 87, 124);;\r\n}\r\n\r\n.head_text:hover i{\r\n  color: rgb(107, 87, 124) !important;\r\n}\r\n\r\n.nav_bar{\r\n  width:100%;\r\n  display: flex;\r\n  position: fixed;\r\n  top:0;\r\n  padding-top: 10px;\r\n  padding-bottom: 10px;\r\n  z-index: 1;\r\n  background-color: hsl(184, 18%, 30%);\r\n}\r\n\r\n.nav_bar ul {\r\n  position: fixed;\r\n  margin:10px 200px;\r\n}\r\n\r\n/*------->> Search */\r\n\r\n.fa-search{\r\n  color:white;\r\n  font-size: 18px;\r\n  padding-top: 12px;\r\n  padding-left: 20px;\r\n}\r\n\r\n.search{\r\n  position: fixed;\r\n  margin-top: 10px;\r\n  right:80px;\r\n  background-color: rgba(0, 0, 0, 0.171);\r\n  border-radius: 10px;\r\n}\r\n\r\n.search input{\r\n  width:400px;\r\n  border:none;\r\n  height:40px;\r\n  font-size: 16px;\r\n  color:white;\r\n  font-weight: 100;\r\n  padding-left: 15px;\r\n  background-color: rgba(255, 255, 255, 0);\r\n}\r\n\r\n.search input:focus{\r\n  border:none;\r\n  outline: none;\r\n}\r\n\r\n::-webkit-input-placeholder { /* Chrome/Opera/Safari */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n\r\n::-moz-placeholder { /* Firefox 19+ */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n\r\n:-ms-input-placeholder { /* IE 10+ */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n\r\n:-moz-placeholder { /* Firefox 18- */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n\r\n/*------->> Login/Register */\