ht: 760px;\r\n  border-radius: 6px;\r\n  padding:20px 0 60px 0;\r\n  background-color: rgba(0, 0, 0, 0.171);\r\n}\r\n\r\n.blog_contain{\r\n  width:100%;\r\n  margin:auto;\r\n}\r\n\r\n.blog_card{\r\n  display:block;\r\n  margin:20px auto;\r\n  width:460px;\r\n  min-height:350px;\r\n  border-radius: 6px;\r\n  background-color: white;\r\n}\r\n\r\n.blog_card h3{\r\n  font-size: 16px;\r\n  text-align: left;\r\n  margin-top: 30px;\r\n  padding-left: 0px;\r\n  color:black;\r\n  font-weight: bold;\r\n  display: inline-block;\r\n}\r\n\r\n.blog_card span{\r\n  margin:0;\r\n  padding:0;\r\n  top:0;\r\n  color:grey;\r\n}\r\n\r\n.card_img{\r\n  background-color: rgb(17, 17, 17);\r\n}\r\n\r\n.img{\r\n  width:100%;\r\n  margin:auto;\r\n}\r\n\r\n.gif{\r\n  width:100%!important;\r\n}\r\n\r\n.post_profile_photo{\r\n  width:50px!important;\r\n  border-radius: 50%;\r\n  display: inline-block;\r\n  margin:15px;\r\n}\r\n\r\n.card_contents{\r\n  width:80%;\r\n  margin:auto;\r\n}\r\n\r\n.card_contents h2, p{\r\n  color:rgb(24, 24, 24);\r\n}\r\n\r\n.card_contents h2{\r\n  padding: 10px 0;\r\n  text-align: center;\r\n  font-weight: bold;\r\n}\r\n\r\n.card_contents p{\r\n  padding-bottom: 5px;\r\n}\r\n\r\n.card_footer{\r\n  width:100%;\r\n  position: relative;\r\n  bottom: -5px;\r\n  background-color: rgb(255, 255, 255);\r\n  height:50px;\r\n  border-bottom-left-radius: 6px;\r\n  border-bottom-right-radius: 6px;\r\n  text-align: right;\r\n}\r\n\r\n.card_footer .fa-scroll{\r\n  color:rgb(109, 109, 109) !important;\r\n  display: inline-block !important;\r\n\r\n}\r\n\r\n.card_footer p{\r\n  display: inline-block;\r\n  color:grey;\r\n  padding-right: 220px;\r\n  padding-top: 10px;\r\n}\r\n\r\n.card_footer i{\r\n  color:rgb(109, 109, 109);\r\n  position: relative;\r\n  padding-top: 15px;\r\n  display: inline-block;\r\n  font-size: 24px;\r\n  margin: 0 10px;\r\n  transition: .5s;\r\n}\r\n\r\n.card_footer i:hover{\r\n  color:rgb(97, 158, 95)!important;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"