module.exports = "\r\n/* <------- Navigation Bar Styling ------->*/\r\nimg{\r\n  position: fixed;\r\n  top:-200px;\r\n  filter: blur(8px);\r\n  -webkit-filter: blur(4px);\r\n  filter: brightness(40%);\r\n}\r\n.nav_bar{\r\n  width:100%;\r\n  display: flex;\r\n  position: fixed;\r\n  margin-top: 20px;\r\n  z-index: 1;\r\n}\r\n.nav_bar ul {\r\n  position: fixed;\r\n  right:100px;\r\n}\r\n/*------->> Search */\r\n.fa-search{\r\n  color:white;\r\n  font-size: 18px;\r\n  padding-top: 12px;\r\n  padding-left: 20px;\r\n}\r\n.search{\r\n  position: relative;\r\n  left:100px;\r\n  background-color: rgba(0, 0, 0, 0.315);\r\n  border-radius: 10px;\r\n}\r\n.search input{\r\n  width:400px;\r\n  border:none;\r\n  height:40px;\r\n  font-size: 16px;\r\n  color:white;\r\n  font-weight: 100;\r\n  padding-left: 15px;\r\n  background-color: rgba(255, 255, 255, 0);\r\n}\r\n.search input:focus{\r\n  border:none;\r\n  outline: none;\r\n}\r\n::-webkit-input-placeholder { /* Chrome/Opera/Safari */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n::-moz-placeholder { /* Firefox 19+ */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n:-ms-input-placeholder { /* IE 10+ */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n:-moz-placeholder { /* Firefox 18- */\r\n  color:rgba(255, 255, 255, 0.884);\r\n}\r\n/*------->> Login/Register */\r\n.nav_bar ul li{\r\n  width:150px;\r\n  height:40px;\r\n  border:1px solid rgb(255, 255, 255);\r\n  text-align: center;\r\n  display: inline-block;\r\n  padding-top: 6px;\r\n  border-radius: 4px;\r\n  margin:0 10px;\r\n  cursor: pointer;\r\n  transition: .5s;\r\n}\r\n.nav_bar ul li a{\r\n  text-decoration: none;\r\n  font-size: 18px;\r\n  text-align: center;\r\n  transition: .5s;\r\n  width: 100px;\r\n}\r\n.about{\r\n  background-color: white !important;\r\n  border:none !important;\r\n  padding-top: 8px !important;\r\n  transition: .5s;\r\n}\r\n.about a {\r\n  color:hsl(184, 24%, 55%) !important;\r\n}\r\n.nav_bar ul li:hover{\r\n  background-color: white;\r\n}\r\n.nav_