ba(255, 255, 255, 0.61);\r\n  border-radius: 3px;\r\n  font-weight: bold;\r\n}\r\n.submit {\r\n  display:block;\r\n  width:100%;\r\n  border-radius: 20px;\r\n  border:none;\r\n  height:40px;\r\n  margin-top: 60px;\r\n  margin-bottom: 15px;\r\n  color: rgb(233, 233, 233) !important;\r\n  text-align: center;\r\n  font-size: 18px;\r\n  background-color: rgba(61, 37, 71, 0.822);\r\n  transition:.5s;\r\n}\r\n.submit:hover{\r\n  background-color: rgb(92, 56, 109);\r\n}\r\n.error {\r\n  text-align: center;\r\n  background-color: rgba(0, 0, 0, 0.2);\r\n  border-radius: 3px;\r\n}\r\n\r\n\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"